@import '../../style/variables.scss';

.single-comic {
    margin-top: 50px !important;
    display: grid;
    grid-template-columns: 293px 550px auto !important;
    column-gap: 50px !important;
    align-items: start !important;
    width: 1100px !important;
    //margin: 0 auto;
    padding: 50px 0 50px 0 !important;

    &__img {
        width: 293px;
        height: 450px;
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
    }
    &__descr {
        font-size: 18px;
        line-height: 24px;
        margin-top: 25px;
    }
    &__price {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        color: $main-color;
        margin-top: 25px;
    }
    &__back {
        justify-self: end;
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        &:hover {
            color: $main-color;
        }
    }
}